@import "./typo.module.css";
@import "./colors.module.css";

.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
  padding: 1rem;
}

.App-subtitle {
  opacity: 0.5;
  font-size: 0.9rem;
}

.App-title {
  color: #614b50;
  font-family: "poppinsbold";
  text-align: center;
  font-size: 2rem;
}

