@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.App-navlink {
  text-decoration: none;
}

/* FORM */

.App-formcontainer {
  color: black;
}

.App-form-h1 {
  margin-top: 2rem;
  font-size: 2rem;
  font-family: "poppinsbold";
  color: var(--brown-title);
  margin-bottom: 2rem;
}

.App-form-text {
  font-size: 1rem;
  line-height: 1.4rem;
}

.App-form-input-container {
  display: flex;
  flex-direction: column;
  margin-right: 2rem;
}

.App-form-label {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  margin-top: 3rem;
}

.App-form-input {
  padding: 0.5rem 1rem;
  margin: 0rem 1rem 1rem 0rem;
  max-width: 20rem;
}

.App-form-input:focus {
  outline: none;
}

.App-img_form {
  background-image: url("../assets/images/form/signup.png");
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  position: relative;
  margin-right: 3rem;
}

.gContainer {
  display: block;
  height: 100%;
  width: 100%;
  /* margin: 3rem 0; */
}

.gImg {
  background-image: url("../assets/images/form/signup.png");
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* PROGRESSTRACKER */

ol.progtrckr {
  margin: 0;
  padding-bottom: 2.2rem;
  list-style-type: none;
  display: flex;
  justify-content: center;
}
ol.progtrckr li {
  display: inline-block;
  text-align: center;
  line-height: 4.5rem;
  padding: 0 0.7rem;
  cursor: pointer;
}
ol.progtrckr li span {
  padding: 0 1.5rem;
  font-size: 1rem;
}
@media (max-width: 650px) {
  .progtrckr li span {
    display: none;
  }
}
.progtrckr em {
  display: none;
  font-weight: 700;
  padding-left: 1rem;
}
@media (max-width: 650px) {
  .progtrckr em {
    display: inline;
  }
}

ol.progtrckr li.progtrckr-todo {
  color: silver;
  border-bottom: 4px solid silver;
}
ol.progtrckr li.progtrckr-doing {
  color: black;
  border-bottom: 4px solid var(--pink-primair);
}
ol.progtrckr li.progtrckr-done {
  color: black;
  border-bottom: 4px solid var(--pink-primair);
}
ol.progtrckr li:after {
  content: "\00a0\00a0";
}
ol.progtrckr li:before {
  position: relative;
  bottom: -3.7rem;
  float: left;
  left: 50%;
}
ol.progtrckr li.progtrckr-todo:before {
  content: "\039F";
  color: silver;
  background-color: white;
  width: 1.2em;
  line-height: 1.4em;
}
ol.progtrckr li.progtrckr-todo:hover:before {
  color: var(--pink-primair);
}

ol.progtrckr li.progtrckr-doing:before {
  content: "\2022";
  color: white;
  background-color: var(--pink-primair);
  width: 1.2em;
  line-height: 1.2em;
  border-radius: 1.2em;
}
ol.progtrckr li.progtrckr-doing:hover:before {
  color: var(--pink-primair);
}

ol.progtrckr li.progtrckr-done:before {
  content: "\2713";
  color: white;
  background-color: var(--pink-primair);
  width: 1.2em;
  line-height: 1.2em;
  border-radius: 1.2em;
}
ol.progtrckr li.progtrckr-done:hover:before {
  color: var(--pink-primair);
}

.App-button_form_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-right: 4rem;
  margin-bottom: 4rem;
}
